import Swiper from 'swiper';

export default class Slider {
	/**
	 * 
	 * @param {*} classToAttach 
	 * @param {*} logger 
	 */
	constructor(classToAttach, logger) {

		//TODO: Parametrizzare il selettore
		window.addEventListener('DOMContentLoaded', this.init());
		//TODO: creare un evento per agganciarci all'emit di highway
		window.addEventListener('NAVIGATE_IN', () => {
			this.init();
		});

	}

	init() {

		var $el = $('.js-slider-primary');

		if ($el.length == 0) {
			//logger.Warn('[DATA][SLIDER][WARN]: Classe non trovata, non istanzio l\'oggetto');
			return;
		}

		var slider = new Swiper($el, {
			//init: false,
			direction: 'horizontal',
			loop: true,
			effect: 'slide',
			autoHeight: false,
			parallax: true,
			preloadImages: false,
			lazy: {
				loadPrevNext: true,
				loadPrevNextAmount: 1,
				loadOnTransitionStart: true,
			},
			autoplay: {
				delay: 3000
			},
			pagination: {
				el: '.js-slider-primary-pagination',
				clickable: true
			},
			navigation: {
				prevEl: '.js-slider-primary-navigation-prev',
				nextEl: '.js-slider-primary-navigation-next'
			},
			slidesPerView: 1,
			spaceBetween: 0,
			breakpoints: {
				// when window width is <= 320px
				320: {
					slidesPerView: 1
				},
				992: {
					slidesPerView: 1
				}
			}
		});

	}

	//private slider = undefined;

	play() {
		// TODO: verificare la varibile se metterla nel constructor

		if (slider === undefined) {
			//logger.Info("[][][]: asjhdgashdgaskdhg")
			return;
		}
		this.slider.autoplay.start();
	}

	stop() {
		this.slider.autoplay.stop();
	}

	destroy() {
		this.slider.destroy();
	}

}
